import React from "react";
import { Tabs, Tab, Paper, Typography, CircularProgress, TextField } from "@material-ui/core";
import API from "../../Services/API";
import ProductGrid from "./ProductGrid";
import ConfirmOrder from "./ConfirmOrder";
import Location from "./Location";
import Payment from "./Payment";
import { Totals, EstimarDescuento } from "../../Services/Calcs";
import Message from "../Message";
import Survey from "./Survey";
import Preferences from "../../Services/Preferences";

export default class Order extends React.PureComponent<{ onMounted: (path: string) => void }, OrderState> {
    constructor(props: any) {
        super(props);
        this.handleTab = this.handleTab.bind(this);
        this.handleAction = this.handleAction.bind(this);
        this.handleValueChange = this.handleValueChange.bind(this);
        this.handleDone = this.handleDone.bind(this);
        this.handleMessage = this.handleMessage.bind(this);
        this.handleSurveyClose = this.handleSurveyClose.bind(this);

        this.handleChange = this.handleChange.bind(this);

        if (props.onMounted) props.onMounted(props.match.path);
        this.state = {
            currentTab: 0,
            productList: [],
            tablaDescuentos: [],
            descuentoAplica: null,
            subtotal: 0,
            numeroTransaccion: "",
            direccion: "",
            ciudad: "",
            sector: "",
            observacion: "",
            referencia: "",
            position: null,
            loading: "articulos",
            surveyId: null,
            msg: null,
        };
    }
    componentDidMount() {
        Promise.all([API.GetProducts(), API.getDiscounts()])
            .then(([productList, tablaDescuentos]) => {
                this.setState({ productList: productList.map(x => { x.cantidad = 0; return x }), tablaDescuentos, loading: null });
                try {
                    const before = Preferences.Get("current_order");
                    if (!before) return;
                    var updates = {}
                    for (const key in before) {
                        if (this.state.hasOwnProperty(key)) {
                            updates[key] = before[key];
                        }
                    }
                    this.setState(updates);
                    if (!(before.choosen && before.choosen.length)) return;
                    for (let i = 0; i < before.choosen.length; i++) {
                        const x = before.choosen[i];
                        for (let times = 0; times < x.cantidad; times++) {
                            this.handleAction({ currentTarget: { name: "+", value: x.id } } as any)
                        }
                    }
                } catch (ex) { }
            }).catch(ex => {
                alert(ex.message)
            });
    }
    handleTab(event: any, currentTab: number) {
        this.setState({ currentTab });
        // actualizar todo!
        try {
            Preferences.Set("current_order", {
                numeroTransaccion: this.state.numeroTransaccion,
                direccion: this.state.direccion,
                ciudad: this.state.ciudad,
                sector: this.state.sector,
                observacion: this.state.observacion,
                referencia: this.state.referencia,
                position: this.state.position,
                choosen: this.state.productList.filter(x => x.cantidad).map(x => ({ id: x.id, cantidad: x.cantidad }))
            });
        } catch (ex) { }
    }
    handleDone() {
        var { subtotal, numeroTransaccion, ciudad, sector, observacion, referencia, direccion, position, descuentoAplica, loading } = this.state
        if (loading) return;
        var choosen = this.state.productList.filter(x => x.cantidad);
        if (!choosen.length) return this.failed_msg("Debes elegir por lo menos un producto");
        if (!(ciudad && ciudad.trim().length)) return this.failed_msg("Digita la ciudad");
        if (!(sector && sector.trim().length)) return this.failed_msg("Digita el sector");
        if (!(direccion && direccion.trim().length)) return this.failed_msg("Digita la direccion");
        if (!(numeroTransaccion && numeroTransaccion.trim().length)) return this.failed_msg("Digita el numero de comprobante de pago");
        this.setState({ loading: "guardar" });
        API.SaveOrder({
            ciudad, sector, direccion, observacion, referencia,
            detalle: choosen,
            descuentoPorcentaje: descuentoAplica && descuentoAplica.porcentaje,
            descuentoTotal: descuentoAplica && descuentoAplica.valor,
            total: subtotal - ((descuentoAplica && descuentoAplica.valor) || 0),
            position, numeroTransaccion
        }).then(x => {
            this.setState({
                currentTab: 0,
                descuentoAplica: null,
                subtotal: 0,
                numeroTransaccion: "",
                observacion: "",
                position: null,
                msg: null,
                loading: null,
                surveyId: x.id,
                productList: this.state.productList.map(x => {
                    x.cantidad = 0;
                    return x;
                })
            });
            Preferences.Set("current_order", {
                direccion: this.state.direccion || "",
                ciudad: this.state.ciudad || "",
                sector: this.state.sector || "",
                referencia: this.state.referencia || ""
            });
        }).catch(ex => {
            this.setState({
                msg: { title: "Ocurri un error", message: ex.message, info: ex.status },
                loading: null
            });
        });
    }
    failed_msg(message, info?) {
        this.setState({
            msg: { title: "No se puede guardar", message, info }
        });
    }
    handleAction(event: React.MouseEvent<HTMLButtonElement>) {
        const { name, value } = event.currentTarget;
        this.setState(prev => {
            const state: any = { ...prev };
            state.productList = JSON.parse(JSON.stringify(state.productList));
            var item: Product = state.productList.find((a: any) => a.id === +value);
            var cant: number = (item.cantidad && !isNaN(+item.cantidad) && item.cantidad) || 0;
            const { cantidad_desde } = item;
            if (name === "+") {
                if (cant <= 0 && cantidad_desde) {
                    cant = cantidad_desde;
                } else {
                    cant++;
                }
            } else if (name === "-") {
                if (cant <= 10 && cantidad_desde) {
                    cant = 0;
                } else {
                    cant--;
                }
            }
            item.cantidad = cant;
            state.subtotal = Totals(state.productList.filter(x => x.cantidad)).total;
            state.descuentoAplica = EstimarDescuento(state.subtotal, state.tablaDescuentos);
            return state;
        });
    }
    handleValueChange(name: string, value: string) {
        this.setState({ [name]: value } as any);
    }
    handleMessage() {
        this.setState({ msg: null });
    }
    handleSurveyClose() {
        this.setState({ surveyId: null });
    }
    handleChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { value, name } = event.currentTarget;
        this.setState({ [name]: value } as any);
    }
    render() {
        const {
            currentTab, productList, numeroTransaccion, descuentoAplica, subtotal,
            msg, surveyId, loading, observacion,
            ...remain_state
        } = this.state;
        const choosenItems = productList.filter((x: any) => x.cantidad);
        console.info("Order render");
        const total = subtotal - ((descuentoAplica && descuentoAplica.valor) || 0);
        if (loading === "articulos") {
            return (<div style={{ width: "100%", textAlign: "center", paddingTop: 100 }}>
                <CircularProgress size={100} />
                <Typography variant="h6">CARGANDO...</Typography>
            </div>);
        }

        return (<div style={{ width: "100%", height: "calc(100% - 1px)", display: "flex", flexDirection: "column", padding: 10, overflow: "hidden" }} >
            <Tabs variant="fullWidth" onChange={this.handleTab} value={currentTab}>
                <Tab label="Productos" />
                <Tab label="Pedido" />
                <Tab label="Direccin" />
                <Tab label="Pago" />
            </Tabs>
            {currentTab === 0 && <ProductGrid visible={true} onActionChanged={this.handleAction} items={productList} />}
            {currentTab === 1 && (<Paper style={{ display: currentTab === 1 ? "block" : "none", textAlign: "center", padding: 10, margin: "auto", marginTop: 10, width: "100%", maxWidth: 500, height: "100%" }}>
                <Typography variant="h5">TU PEDIDO</Typography>
                <ConfirmOrder show_next_discount={true} items={choosenItems} descuento={descuentoAplica} subtotal={subtotal} total={total} />
                <TextField variant="outlined" value={observacion} name="observacion" onChange={this.handleChange} style={{ width: "calc(100% - 20px)", margin: 10 }} label="Observacion" multiline rows="2" />
            </Paper>)}
            <Location visible={currentTab === 2} {...remain_state} onValueChange={this.handleValueChange} />
            {currentTab === 3 && <Payment visible={true} loading={loading === "guardar"} total={total} onDone={this.handleDone} items={choosenItems} numeroTransaccion={numeroTransaccion} onValueChange={this.handleValueChange} />}


            {msg && <Message message={msg.message} info={(msg.status || "") + (msg.code || "")} onClose={this.handleMessage} title={msg.title} />}
            {surveyId && <Survey title="Tu pedido fue guardado con exito" id={surveyId} onClose={this.handleSurveyClose} />}
        </div >)
    }
}


interface OrderState {
    currentTab: number,
    productList: Product[],
    tablaDescuentos: Descuento[],
    descuentoAplica: DescuentoCalc;
    subtotal: number;
    position: { lat: number, lng: number };
    direccion: string;
    sector: string;
    ciudad: string;
    referencia: string;
    observacion: string;
    numeroTransaccion: string;
    loading: "articulos" | "guardar";
    msg: any;
    surveyId?: number;
}