import React from "react";
import { Card, CardContent, TextField, Button, InputAdornment, IconButton, Typography } from "@material-ui/core";
import API from "../Services/API";
import { Visibility, VisibilityOff } from "@material-ui/icons";

export default class Login extends React.Component<{ onLoggedIn: (params: any) => void }, LoginState> {
    state = {
        loading: false,
        user: "",
        pwd: "",
        error: null,
        isPasswordVisible: false
    }
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
        API.KEY = "";
    }
    handleChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { name, value } = event.currentTarget;
        this.setState({
            [name]: value
        } as any);
    }
    componentDidMount() {
        setTimeout(() =>
            window.requestAnimationFrame(function () {
                document.getElementById("user-textfield").focus();
            }), 100);
    }
    handleSubmit(event: any) {
        event.preventDefault();
        if (this.state.loading) return;
        const user = this.state.user && this.state.user.toString().trim();
        const pwd = this.state.pwd && this.state.pwd.toString().trim();
        if (!user) return alert("Digite su usuario");
        if (!pwd) return alert("Digite su contrasea");
        this.setState({ loading: true, error: null });
        API.Login({ user, pwd })
            .then(r => {
                this.setState({ loading: null });
                this.props.onLoggedIn({ ...r });
            })
            .catch(error => this.setState({ loading: null, error }));
    }
    handlePassword(event) {
        this.setState(prev => {
            const state = { ...prev };
            state.isPasswordVisible = !state.isPasswordVisible;
            return state;
        });
    }
    render() {
        const { loading, error, user, pwd, isPasswordVisible } = this.state;
        return (<div style={{ paddingTop: 20 }}>
            <Card style={{ width: "100%", maxWidth: 300, margin: "auto" }}>
                <form onSubmit={this.handleSubmit}>
                    <CardContent>
                        <img src="http://natucosmetic.com/wp-content/uploads/2020/03/LOGO-NATU-PNG.png" style={{ width: "100%", maxHeight: "40vh" }} ></img>
                        <TextField
                            inputProps={{ id: "user-textfield" }}
                            onChange={this.handleChange}
                            value={user}
                            label="Usuario"
                            name="user"
                            fullWidth
                            disabled={loading}
                            style={defaultStyle}
                        />
                        <TextField
                            onChange={this.handleChange}
                            value={pwd}
                            name="pwd"
                            label="Contrasea"
                            type={isPasswordVisible ? "input" : "password"}
                            fullWidth
                            disabled={loading}
                            style={defaultStyle}
                            InputProps={{
                                endAdornment: <InputAdornment position="end">
                                    <IconButton onClick={this.handlePassword}>
                                        {isPasswordVisible ? <VisibilityOff /> : <Visibility />}
                                    </IconButton>
                                </InputAdornment>
                            }}
                        />
                        {error && <Typography color="secondary" variant="body1" style={{ textAlign: "center" }}>{error.message}</Typography>}
                        <Button type="submit" variant="outlined" style={defaultStyle} color="primary" disabled={loading} fullWidth >
                            {loading ? "CARGANDO..." : "ENTRAR"}
                        </Button>
                    </CardContent>
                </form>
            </Card>
        </div>)
    }
}
const defaultStyle = {
    marginTop: 10,
    marginBottom: 10
}

class LoginState {
    user = "";
    pwd = "";
    loading = false;
    isPasswordVisible = false;
    error = null;
}