import React from "react";
import { CardContent, CardHeader, Card } from "@material-ui/core";
import Ranking from "../Ranking";
export default function RankingCard(props: {
    data: Rank[],
    title?: string,
    showLastOrder?: boolean,
    perfomanceStat?: boolean
}) {
    return (<Card className="dashboard-card" style={{ display: "flex", flexDirection: "column" }} >
        <CardHeader title={props.title} style={{ textAlign: "center" }} />
        <CardContent style={{ flex: 1, overflow: "auto" }}>
            <Ranking data={props.data} showLastOrder={props.showLastOrder} perfomanceStat={props.perfomanceStat} />
        </CardContent>
    </Card>)
}