import React from "react";
import { withGoogleMap, withScriptjs, GoogleMap } from "react-google-maps";
import { TextField, Paper } from "@material-ui/core";
import Geo from "../../Services/Geo";
export default class Location extends React.PureComponent<{
    direccion: string,
    ciudad: string,
    sector: string,
    referencia: string,
    position: { lat: number, lng: number },
    visible: boolean,
    onValueChange: (prop: string, value: string) => void
}, {}> {
    state = {
        defaultCenter: null
    }
    mapRef;
    virtualMarker;
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleMapMounted = this.handleMapMounted.bind(this);
        this.handleDragStart = this.handleDragStart.bind(this);
        this.handleDragEnd = this.handleDragEnd.bind(this);
    }
    componentWillMount() {
        Geo.currentPosition()
            .then(x => this.setState({ defaultCenter: x }))
    }
    handleChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { name, value } = event.currentTarget;
        this.props.onValueChange(name, value);
    }
    handleDragStart() {
        this.virtualMarker.classList.add("on-move");
    }
    handleDragEnd() {
        Geo.PlaceFromCoords(this.mapRef.getCenter())
            .then(d => this.props.onValueChange("direccion", d.inputText))
            .catch(ex => console.warn("PlaceFromCoords ERROR", ex));
        this.virtualMarker.classList.remove("on-move");
    }
    handleMapMounted(map) {
        this.mapRef = map;
        if (!this.mapRef) return;
        this.virtualMarker = document.createElement("div");
        this.virtualMarker.className = "centerMarker";
        map.getDiv().appendChild(this.virtualMarker);
    }
    render() {
        const { direccion, ciudad, referencia, sector, visible } = this.props;
        return (<Paper style={{ margin: "auto", height: "100%", width: "100%", maxWidth: 500, padding: 10, marginTop: 10, display: visible ? "block" : "none" }}>

            <TextField
                variant="outlined"
                name="ciudad"
                value={ciudad}
                onChange={this.handleChange}
                label="Ciudad*"
                style={{ margin: 10, width: "calc(100% - 20px)" }}
            />

            <TextField
                variant="outlined"
                name="sector"
                value={sector}
                onChange={this.handleChange}
                label="Sector*"
                style={{ margin: 10, width: "calc(100% - 20px)" }}
            />

            <TextField
                variant="outlined"
                name="direccion"
                value={direccion}
                onChange={this.handleChange}
                label="Direccion Exacta*"
                multiline
                rows={3}
                style={{ margin: 10, width: "calc(100% - 20px)" }}
            />

            <TextField
                variant="outlined"
                name="referencia"
                value={referencia}
                onChange={this.handleChange}
                label="Referencia"
                multiline
                rows={2}
                style={{ margin: 10, width: "calc(100% - 20px)" }}
            />

        </Paper>)
    }
}
const Map = withScriptjs(withGoogleMap(function (props: any) {
    const { onMounted, ...google_maps_props } = props;
    return (<GoogleMap
        ref={onMounted}
        {...google_maps_props}
        options={{
            disableDefaultUI: true,
            zoomControl: true,
            clickableIcons: false
        }}
    >

    </GoogleMap>)
}));

/***
 *
            {defaultCenter && <Map
                googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyDxc5H1-I6rNF0PZH4Kiw2YsW0kMCWC9_Q&v=3.exp&libraries=geometry,drawing,places"
                loadingElement={<div style={{ height: `100%` }} />}
                containerElement={<div id="location-map-container" />}
                mapElement={<div style={{ height: `100%` }} />}
                defaultCenter={defaultCenter}
                defaultZoom={15}
                onMounted={this.handleMapMounted}
                onDragStart={this.handleDragStart}
                onDragEnd={this.handleDragEnd}
            />}
 */