import React from "react";
import { Grid, CircularProgress, Typography, Hidden, Divider } from "@material-ui/core";
import RankingCard from "./RankingCard";
import API from "../../Services/API";
import GroupCard from "./GroupCard";
import ProfitCard from "./ProfitCard";
import PieCard from "./PieCard";
export default class Feed extends React.Component<{ onMounted: Function, grupo: string, username: string, rol: number }> {
    state = {
        loading: true,
        error: null,
        week_ranking: [],
        group_graph: [],
        personal_graph: [],
        year_sales: [],
        product_resume: [],
        group_ranking: []
    }
    constructor(props) {
        super(props);
        props.onMounted(props.match.path);
    }
    componentWillMount() {
        API.Stats(["week_ranking",
            "personal_graph",
            "group_graph",
            "year_sales",
            "product_resume",
            "group_ranking"])
            .then(x => this.setState({ ...x, loading: null }))
            .catch(error => {
                this.setState({ error, loading: null });
            });
    }
    render() {
        var { loading, error,
            week_ranking,
            personal_graph,
            group_graph,
            year_sales,
            product_resume,
            group_ranking
        } = this.state;
        const { rol, grupo, username } = this.props;
        const week_progress = week_ranking.map((x, index) => ({ ...x, stat: x.perfomance, position: index + 1 }));
        const cumulative_ranking = week_ranking.map((x, index) => ({ ...x, stat: x.monto_acumulativo, position: index + 1 }));

        var resume = { month: 0, year: 0 };
        var ahora = new Date();
        for (let i = 0; i < year_sales.length; i++) {
            const x = year_sales[i];
            x.createdAt = new Date(x.createdAt);
            if (x.createdAt.getMonth() === ahora.getMonth() &&
                x.createdAt.getFullYear() === ahora.getFullYear()) {
                resume.month += x.profit;
            }
            resume.year += x.profit;
        }

        if (loading) return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
            <CircularProgress size={100} />
            <Typography variant="h6">CARGANDO...</Typography>
        </div>);

        if (error) return (<div style={{ width: "100%", paddingTop: 100, textAlign: "center" }}>
            <Typography variant="h5">OCURRI UN PROBLEMA</Typography>
            <Typography variant="subtitle1">{error.message}</Typography>
            <Typography variant="body1">{error.status} {error.code}</Typography>
        </div>);

        return (<Grid container spacing={2} style={{ margin: "auto", width: "calc(100% - 10px)", textAlign: "center" }} >
            <Grid item xs={12}>
                <Typography color="primary" variant="h6">
                    Hola {username}, Esta es tu cuenta personal de Nat.
                </Typography>
                <Typography variant="subtitle1">
                    Estamos aqu para prosperar juntos. Estos son los indicadores de tu trabajo.
                </Typography>
            </Grid>

            <Grid item sm={6} xs={12}>
                <RankingCard data={group_ranking} title="RANKING DE GRUPOS" />
            </Grid>

            <Grid item sm={6} xs={12}>
                <GroupCard data={personal_graph} title={"ESTADISTICA SEMANAL"} />
            </Grid>

            <Grid item sm={6} xs={12}>
                <GroupCard data={group_graph} title={grupo || "ESTADISTICA MENSUAL"} />
            </Grid>

            <Grid item sm={6} xs={12}>
                <PieCard data={product_resume} title="PRODUCTOS VENDIDOS" />
            </Grid>

            <Grid item sm={6} xs={12}>
                <ProfitCard month={resume.month} year={resume.year} />
            </Grid>

            <Hidden smUp>
                <Grid item xs={12}>
                    <Divider />
                </Grid>
            </Hidden>

            <Grid item sm={6} xs={12}>
                <RankingCard data={week_ranking} title="RANKING DE LA SEMANA" showLastOrder={rol !== 1} />
            </Grid>

            <Grid item sm={6} xs={12}>
                <RankingCard data={week_progress} title="PROGRESO DE LA SEMANA" perfomanceStat={true} />
            </Grid>

            {rol !== 1 && <Grid item sm={6} xs={12}>
                <RankingCard data={cumulative_ranking} title="RANKING ACUMULATIVO" />
            </Grid>}


        </Grid>);
    }
}