import React from "react";
import { Grid, Card, CardContent, CardActions, CardMedia, Typography, IconButton } from "@material-ui/core";
import { Add, Remove } from "@material-ui/icons";

export default function
    ProductGrid(props: { onActionChanged: any, items: any[], visible: boolean }) {
    const visible_style: any = {}
    if (!props.visible) visible_style.display = "none";
    return (<Grid
        container
        alignItems="center"
        style={{ flex: 1, overflow: "auto", marginTop: 5, ...visible_style }}
        spacing={3}>
        {props.items.map((item: any) => (<Grid key={item.id} item sm={6} md={4} xs={12}>
            <Card>
                <CardMedia component="img" style={{ height: 180, objectFit: "contain" }} image={item.imgSrc || "https://diegopq95.github.io/take-orders/img/default.png"} />
                <CardContent style={{ display: "flex", flexDirection: "row" }}>
                    <Typography style={{ flex: 1, height: 60, overflow: "hidden" }} variant="subtitle1">{item.nombre}</Typography>
                    <Typography variant="h6">$ {item.pvp_final.toFixed(2)}</Typography>
                </CardContent>
                <CardActions>
                    <IconButton onClick={props.onActionChanged} name="+" value={item.id}>
                        <Add />
                    </IconButton>
                    <Typography variant="body1">{item.cantidad}</Typography>
                    <IconButton disabled={!(item.cantidad > 0)} onClick={props.onActionChanged} name="-" value={item.id}>
                        <Remove />
                    </IconButton>
                </CardActions>
            </Card>
        </Grid>))}
    </Grid>)
}