var GeoCoder;
class GeoLocation {
    currentPosition() {
        return new Promise((resolve, reject) => {
            console.info("obteniendo posicion actual con navigator.geolocation.getCurrentPosition");
            if (!navigator.geolocation) {
                console.info("navigator.geolocation.getCurrentPosition no existe en este navegador, intentando con PositionByIP()");
                return PositionByIP().then(resolve).catch(reject);
            }
            navigator.geolocation.getCurrentPosition(geoLocation => {
                console.info("navigator.geolocation.getCurrentPosition ha obtenido la posicion");
                resolve({
                    lat: geoLocation.coords.latitude,
                    lng: geoLocation.coords.longitude,
                    fromGPS: true
                });
            }, () => {
                console.info("navigator.geolocation.getCurrentPosition fall, intentando con PositionByIP()");
                PositionByIP().then(resolve).catch(reject);
            }
            );
        });
    }
    CanUseGPS() {
        return new Promise((resolve, reject) => {
            if (!navigator.geolocation) return reject();
            navigator.geolocation.getCurrentPosition(resolve, reject);
        });
    }
    PlaceFromCoords(location, bounds?): Promise<{ inputText: string }> {
        const { google } = window as any;
        if (!GeoCoder) GeoCoder = new google.maps.Geocoder();
        return new Promise((resolve, reject) => {
            GeoCoder.geocode({ location, bounds: bounds || this.QUITO_BOUNDS }, (results, status) => {
                if (status === "ZERO_RESULTS") {
                    return reject({ status, message: "No se encontraron resultados" });
                }
                const first = results.length && results[0];
                if (!first) return reject({ message: "No se encontraron resultados" });
                first.inputText = AddressText(first);
                resolve(first);
            });
        })
    }
    CoordsFromPlace(address, bounds?) {
        const { google } = window as any;
        if (!GeoCoder) GeoCoder = new google.maps.Geocoder();
        return new Promise((resolve, reject) => {
            GeoCoder.geocode({ address, bounds: bounds || this.QUITO_BOUNDS }, (results, status) => {
                if (status === "ZERO_RESULTS") {
                    return reject({ status, message: "No se encontraron resultados" });
                }
                const first = results.length && results[0];
                if (!first) return reject({ message: "No se encontraron resultados" });
                resolve(first);
            });
        })
    }
    QUITO_BOUNDS = {
        west: -78.85300,
        north: -0.04875,
        east: -78.17184,
        south: -0.39207
    }
    QUITO = {
        lat: -0.189515,
        lng: -78.488167
    }
}
export default new GeoLocation();


function AddressText(place) {
    var route;
    var street_number;
    for (let i = 0; i < place.address_components.length; i++) {
        const component = place.address_components[i];
        for (let ii = 0; ii < component.types.length; ii++) {
            const type = component.types[ii];
            if (type === "street_address") return component.short_name || component.long_name;
            if (component.types.find(x => x === "street_number") && component.types.find(x => x === "route"))
                return component.short_name || component.long_name;

            if (type === "route" && !route) route = component.short_name || component.long_name;
            if (type === "street_number" && !street_number) street_number = component.short_name || component.long_name;
        }
    }
    if (route && street_number) return route + " " + street_number;
    return place.formatted_address;
}
function PositionByIP() {
    return fetch("https://ipapi.co/json/").then(x => x.json())
        .then(info => ({ lat: info.latitude, lng: info.longitude }));
}