import React from "react";
import { Paper, Typography, TextField, Button, Divider, CircularProgress } from "@material-ui/core";
export default function (props: {
    onValueChange: Function,
    numeroTransaccion: string,
    items: any[],
    onDone: any,
    total: number,
    loading: boolean,
    visible: boolean
}) {

    return (<Paper style={{ maxWidth: 400, padding: 10, height: "100%", marginLeft: "auto", marginRight: "auto", display: props.visible ? "block" : "none" }}>
        <Typography style={{ marginTop: 10, marginBottom: 10, textAlign: "center" }} variant="h6">Total a Pagar: ${props.total.toFixed(2)}</Typography>
        <Divider />
        <div style={{ padding: 20 }}>
            <Typography>Realiza tu pago mediante Transferencia o Deposito Bancario a la cuenta:</Typography>
            <Typography>BANCO DE PICHINCHA. Cta. Cte. #123123123 </Typography>
            <Typography>C.I. 0932050222</Typography>
            <Typography>Natu Cosmetics S.A</Typography>
        </div>

        <TextField
            style={{ width: "calc(100% - 20px)", margin: 10 }}
            variant="outlined"
            inputMode="numeric"
            onChange={event => props.onValueChange("numeroTransaccion", event.currentTarget.value)}
            value={props.numeroTransaccion}
            label="Numero de Comprobante"
        />
        <Button disabled={props.loading} onClick={props.onDone} variant="outlined" color="primary" style={{ margin: 10, width: "calc(100% - 20px)" }}>
            {props.loading ? <CircularProgress size={25} /> : "GUARDAR"}
        </Button>
    </Paper>)
}