import React from "react";
import { List, ListItem, Typography } from "@material-ui/core";
export default class Ranking extends React.PureComponent<{ data: Rank[], showLastOrder?: boolean, perfomanceStat?: boolean }> {
    render() {
        const { data, showLastOrder, perfomanceStat } = this.props;
        return (<List>
            {data.sort((a, b) => b.stat - a.stat)
                .map((r, index) => (<ListItem key={index} id={"ranking-listitem-" + index} >
                    <div style={{ display: "table", width: "100%" }} >
                        <Typography style={{ display: "table-cell", width: 50 }} variant={r.me ? "body1" : "caption"}># {index + 1}</Typography>
                        <Typography style={{ display: "table-cell" }} variant={r.me ? "body1" : "caption"} >{r.username}</Typography>
                        <Typography color={perfomanceStat && r.stat ? (r.stat > 0 ? "primary" : "secondary") : "inherit"} style={{ display: "table-cell", textAlign: "right", width: 100 }} variant={r.me ? "body1" : "caption"}>
                            {perfomanceStat ? "%" : "$"} {r.stat.toFixed(2)} {perfomanceStat && r.stat ? (r.stat > 0 ? "" : "") : null}
                        </Typography>
                        {showLastOrder &&
                            <Typography style={{ display: "table-cell", textAlign: "right", width: 100, paddingLeft: 10 }} variant={r.me ? "body1" : "caption"}>{DateToString(r.ultimoPedido)}</Typography>}
                    </div>
                </ListItem>))}
        </List>)
    }
}
function DateToString(x) {
    if (!x) return "Nunca";
    var date = new Date(x);
    var ahora = new Date();
    if (date.getDate() === ahora.getDate() &&
        date.getMonth() === ahora.getMonth() &&
        date.getFullYear() === ahora.getFullYear()) {
        return date.toLocaleTimeString();
    }

    for (let i = 1; i < 30; i++) {
        var este_dia = new Date(ahora.getFullYear(), ahora.getMonth(), ahora.getDate() - i);
        if (date.getDate() === este_dia.getDate() &&
            date.getMonth() === este_dia.getMonth() &&
            date.getFullYear() === este_dia.getFullYear()) {
            if (i === 1) return "Ayer";
            return "Hace " + i + " das";
        }
    }
    return date.toLocaleDateString();
}