const DOMAIN = "https://natuapp.ue.r.appspot.com";
//const DOMAIN = "http://localhost:9000";
class API {
    KEY = "";
    Login(body) {
        return this.Fetch("user/login", { body, method: "POST" });
    }

    GetProducts() {
        return this.Fetch("product");
    }

    getDiscounts(): Promise<Descuento[]> {
        return Promise.resolve([
            { montoDesde: 60, porcentaje: 20 },
            { montoDesde: 250, porcentaje: 25 },
            { montoDesde: 500, porcentaje: 30 }
        ]);
    }

    GetOrders(): Promise<Order[]> {
        return this.Fetch("order");
    }
    GetOrder(id: number): Promise<Order> {
        return this.Fetch("order/" + id);
    }
    SaveOrder(body: Order): Promise<any> {
        return this.Fetch("order", {
            method: "POST",
            body
        });
    }
    ActionOrder(id: number | string, body: { action: "SURVEY" | "APROBAR" | "RECHAZAR" | "ENTREGAR", value: any }): Promise<any> {
        return this.Fetch("order/" + id, {
            method: "PUT",
            body
        });
    }

    Stats(array_items) {
        return this.Fetch("dashboard?data=" + array_items.join(","));
    }
    ProductSalesReport(params: { desde: string, hasta: string, estado: any }) {
        var param_str = "";
        if (params && Object.keys(params).length)
            param_str = "?" + new URLSearchParams(params).toString();

        return this.Fetch("reports/product-sales" + param_str);
    }

    private Fetch(URI, options: SpecialRequest = {}) {
        const DEFAULTS: any = {
            method: "GET",
            cache: "reload",
            mode: "cors"
        };
        var ops: RequestInit = { ...DEFAULTS, ...options };
        if (!ops.headers) ops.headers = {};
        ops.headers['Content-Type'] = 'application/json';
        ops.headers['Access-Control-Allow-Origin'] = '*';

        if (this.KEY) {
            if (!ops.headers) ops.headers = {};
            ops.headers["Authorization"] = 'Basic ' + this.KEY;
        }

        if (ops.body && (typeof ops.body !== typeof ""))
            ops.body = JSON.stringify(ops.body);

        return fetch(DOMAIN + "/" + URI, ops).then(response => {
            if (response.ok) return response.json();
            return response.clone().json()
                .catch(() => {
                    return response.text().then(x => {
                        throw {
                            message: x,
                            status: response.status,
                            statusText: response.statusText
                        }
                    })
                }).then(obj => {
                    throw {
                        ...obj,
                        status: response.status,
                        statusText: response.statusText
                    }
                })
        }).catch(ex => {
            if (ex.message === "Failed to fetch") {
                throw {
                    message: "Este servicio no se encuentra disponible por el momento. Intentelo ms tarde.",
                    status: 0
                }
            }
            if (!ex.message) ex.message = ex.statusText;
            throw ex;
        });
    }
}
export default new API();

interface SpecialRequest extends RequestInit {
    body?: any
}